<template>
    <div class="v-container">
        <el-breadcrumb>
            <el-breadcrumb-item>设置</el-breadcrumb-item>
        </el-breadcrumb>
        <hr>
        <div class="setting-container">
            <el-row :gutter="30">
                <el-col :span="8" v-for="settingInfo in settingInfos" :key="settingInfo.link">
                    <div class="setting-content">
                        <router-link :to="settingInfo.link">
                            <div class="setting-info">
                                <i :class='[settingInfo.classObj]'></i>
                                <h4>{{settingInfo.title}}</h4>
                            </div>
                        </router-link>
                    </div>
                </el-col>
            </el-row>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'setting',
        data() {
            return {
                settingInfos: [
                    {
                        classObj: 'iconfont icon-daimabiaoguanli set-ico',
                        title: "代码表管理",
                        link: "/setting/codeManage"
                    },
                    {
                        classObj: 'iconfont icon-fuwuqiguanli set-ico',
                        title: "服务器管理",
                        link: "/setting/serverManage"
                    },
                    {
                        classObj: 'iconfont icon-gongjuguanli set-ico',
                        title: "工具管理",
                        link: "/setting/toolManage"
                    },
                    {
                        classObj: 'iconfont icon-yonghuguanli set-ico',
                        title: "用户管理",
                        link: "/setting/userManage"
                    },
                    {
                        classObj: 'iconfont icon-guanli set-ico',
                        title: "项目管理",
                        link: "/setting/projectManage"
                    },
                    {
                        classObj: 'iconfont icon-xitongguanli set-ico',
                        title: "系统管理",
                        link: "/setting/sysManage"
                    },
                ]
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .el-col {
        padding: 20px;
    }

    .setting-container {
        padding: 20px;
    }

    .setting-content {
        height: 150px;
        background: #ffffff;
        text-align: center;
        border-radius: 15px;
        box-shadow: 0 4px 10px rgba(0, 0, 0, 0.5);
    }

    .setting-info {
        padding-top: 26px;
        cursor: pointer;
    }

    .set-ico {
        padding-top: 26px;
        font-size: 48px;
    }

    /*  .el-breadcrumb{
        font-size: 12px !important;
      }*/
</style>



